import { Component, OnInit, transition, style, trigger, animate } from '@angular/core';
import { TorneioService } from '../../services/torneio.service';
import { SocketService } from '../../services/socket.service';


@Component({
  selector: 'app-resultado',
  templateUrl: './resultado.component.html',
  styleUrls: ['./resultado.component.css'],
  animations: [
    trigger('fadeIn', [
      transition(':enter', [
        style({ opacity: '0' }),
        animate('1s ease-out', style({ opacity: '1' })),
      ]),
    ]),
  ],
})
export class ResultadoComponent implements OnInit {
  performances: any = [];
  name: string = "";
  grupoDaFase: string = "";
  currentTournament: any;
  currentStage: any;
  ordem: any = 0;
  fieldValue:string="Musica 1";
  public category = false;
  public options = false;
  public op1 = false;
  public op2 = false;
  public op3 = false;
  public tournaments: any = [];
  currentNav=0;
  constructor(public torneioService: TorneioService,
    private socket: SocketService) {

  }

  ngOnInit() {
    this.torneioService.getAllTournaments().subscribe((res) => {
      console.log(res.json())
      this.tournaments = res.json();

      this.socket.on("score-update")
        .subscribe(data => {
          this.updateScore(data)
        })
    })
  }

  updateScore(data) {
    console.log("data", data)
    this.performances.forEach((item, index) => {
      console.log("item", item)

      if (item.id == data.id) {
        this.performances[index].score = data.score;
        this.performances[index].scoreChanged = true;
        setTimeout(() => {
          this.performances[index].scoreChanged = false
        }, 400)
      }
    });
  }


  showCategory() {
    if (!this.category) {
      this.category = true;
    } else {
      this.category = false;
    }
    return this.category;
  }

  showOptions() {

    if (!this.options) {
      this.category = true;
      this.options = true;
    } else {
      this.options = false;
    }
    return this.category;
  }
  setCurrentTournament(tournament,index) {
    this.currentNav = index;
    this.torneioService.getPerformancesByTournament(tournament).subscribe((res) => {

      this.performances = res.json();
      console.log(this.performances.length)
    })
    this.torneioService.getTournamentById(tournament).subscribe((res) => {
      console.log(res.json())
      this.currentTournament = res.json();
      this.currentStage = this.currentTournament.stages[0];
    })
  }

  setCurrentStage(event: Event, stage) {
    event.preventDefault()
    this.currentStage = stage;

  }
  deletePerformance(performance) {
    this.torneioService.deletePerformance(performance.id).subscribe((res) => {

      var index = this.performances.indexOf(performance);
      delete this.performances[index];


    })
  }
  cadastrarNaFase(player) {
    this.torneioService.cadastrarPlayerNaFase({
      player: player.id,
      modifier: { group: this.grupoDaFase, stage: this.currentStage.name },
      song: "Musica 1",
      score: -1,
      tournament: this.currentTournament.id,
      ordem: this.ordem

    }).subscribe((res) => {
      var performanceNow = res.json();
      performanceNow.player = player
      this.performances.push(performanceNow);
      this.torneioService.cadastrarPlayerNaFase({
        player: player.id,
        modifier: { group: this.grupoDaFase, stage: this.currentStage.name },
        song: "Musica 2",
        score: -1,
        tournament: this.currentTournament.id,
        ordem: this.ordem

      }).subscribe((res) => {
        var performanceNow = res.json();
        performanceNow.player = player
        this.performances.push(performanceNow);
      });

    })

  }
  submitScore(performance) {
    this.torneioService.editPerformance(performance).subscribe((res) => {
      alert("score enviado")
    });
  }
}



