import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import * as io from 'socket.io-client';
import { Subscribable, Observable } from 'rxjs/Observable';

@Injectable()
export class SocketService {
  private _path = environment.origin
  socket: any;
  constructor() {
    this.socket = io.connect(this._path)
  }

  emmit(event, data?) {
    this.socket.emmit(event, data)
  }

  broadcast(event, data?) {
    this.socket.broadcast.emmit(event, data)
  }

  on(event: string): Subscribable<any> {
    return new Observable(observer => {
      this.socket.on(event, (data: any) => observer.next(data))
    })
  }

}



