import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterBy',
  pure: false

})
export class FilterByScorePipe implements PipeTransform {

  transform(items: any[], field: string, value: string) {
    if (!items) return items;
    value = this.escapeRegExp(value)
    return items.filter(item => new RegExp(value, 'g').test(item[field].toLowerCase()));

  }
  
  escapeRegExp(string = '') {
    string = string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $ & means the whole matched string
    return string.toString().toLowerCase()
  }

}



