import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sortByScore',
  pure:false
  
})
export class SortByScorePipe implements PipeTransform {

   transform(items:any[]) {
    return items.sort(function (a, b) {
      if (a.score > b.score) {
        return -1;
      }
      if (a.score < b.score) {
        return 1;
      }
      // a must be equal to b
      return 0;
    });
  }

}



