import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.css']
})
export class PlayerComponent implements OnInit {

  public player = false;

  constructor(
  ) {}

  ngOnInit() {
  }

  showPlayer(){
    if (!this.player){
      this.player = true;
    } else {
      this.player = false;
    }
    return this.player;
  }

}



