import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'namePipe',
  pure:false
  
})
export class NamePipePipe implements PipeTransform {

   transform(players:any[],name) {
    return players.filter(player=>player.name.indexOf(name)==0);
  }

}



