import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'myNewPipe',
  pure:false
})
export class MyNewPipePipe implements PipeTransform {

 transform(performances:any[],stage) {
    return performances.filter(performance=>performance.modifier.stage==stage);
  }

}



