import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Http, RequestOptions, HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { TorneioService } from "./services/torneio.service";
import { SocketService } from "./services/socket.service";

import { AppComponent } from './app.component';
import { TorneiosComponent } from './pages/torneios/torneios.component';
import { PlayerComponent } from './player/player.component';
import { MyNewPipePipe } from './pipes/my-new-pipe.pipe';
import { NamePipePipe } from './pipes/name-pipe.pipe';
import { ResultadoComponent } from './pages/resultado/resultado.component';
import { AppRoutingModule } from "./app-route.module";
import { environment } from '../environments/environment';
import { SortByScorePipe } from "./pipes/sort-by-score.pipe";
import { FilterByScorePipe } from "./pipes/filter-by-score.pipe";



@NgModule({
  declarations: [
    AppComponent,
    TorneiosComponent,
    PlayerComponent,
    MyNewPipePipe,
    NamePipePipe,
    ResultadoComponent,
    SortByScorePipe,
    FilterByScorePipe
  ],
  imports: [
    BrowserModule,
    [HttpModule],
    FormsModule,
    AppRoutingModule,
    BrowserAnimationsModule
  ],
  providers: [
    TorneioService,
    SocketService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



